import request from '@/utils/request'
// 登陆
export const userLogin=(params={})=>{
    return request({
        url:'/login',
        method:'post',
        data:params
    })
}

// 这里是mock的接口，实际开发项目，后端只有一个接口，后端已经根据用户登陆角色，进行返回不同的 菜单和路由了

