// 二次封装axios
//配置axios
import axios from "axios"
// element加载动画
// 单独引入，饿了么消息框
import { ElLoading ,ElMessage   } from 'element-plus'
// 单独的js 文件 是不能直接使用全局的方法的，全局的方法只能使用项目中mian.js注册全局的组件原型上，只能在.vue组件中使用全局方法，
// 想要在单独的js文件中使用全局方法，必须单独引入，这里的this.$router.push()路由的使用，就需要引入导出的路由router文件夹下面的index.js文件
import router from '../router'
import {getToken} from './isNotHasToken'

let loadingIns=null
// 使用实例创建
let request = axios.create({

    //1 vueCli中环境变量环境变量 
    // 这些变量 在不同环境下值不同环境变量存储在 process.env这个对象下使用 NODE_ENV判断开发环境还是生产环境 定义 baseURL
    //  开发环境是跨域环境 上线后不跨域的 和 接口部署在同一源 服务器下baseURL要区分开发和生产
    //  开发环境 baseURL应该为反向代理请求前缀，但是请求前缀不能 直接 在request.js直接赋值应该存储在环境变量中
    // baseURL: process.env.NODE_ENV==='development'?"/api":'生产源地址头',

    // 2自定义环境变量
    // vueCli中
    // 在项目根目录下创建 如下文件env.development // 定义环境变量 在 开发环境下的值env.production // 定义环境变量 在生产环境下的值
    // 定义环境变量的语法是
    // key=v
    // 注意:自定义环境变量 一定要以-VUE APP 环境变量名开头
    // 记得 重启项目生效

    // 这里process.env.VUE_APP_BASE_UR指的是 .env.development 和 .env.production两个文件里的变量VUE_APP_BASE_URL 对应的 生产和开发 环境的地址
    // #这是基础请求地址，这个项目中的VUE_APP_BASE_URL='/api',这是基础请求地址，用在axios二次封装的baseURL中
    baseURL:process.env.VUE_APP_BASE_URL,
    timeout: 1000 * 15,//超时设置
    withCredentials: true,//携带本地凭据 比如cookie
})

//全局的请求拦截器 对接口的请求批量进行统一处理

request.interceptors.request.use(config => {
    // element加载动画
    loadingIns=ElLoading.service({ 
        lock:true,
        text:'加载中',
        background:'rgba(149, 149, 149, 0.8)'
     })

    //先获取本地的token,添加到请求头中,然后携带请求头发送给后端
    // let token = storage.get('qf-token')
    // console.log(token)
    // token,添加到请求头中
    // config.headers['authorization'] = token;
    // config就是所有的请求配置

    // 获取token 添加到请求头中 发送后端进行接口校验
    if (getToken()) {
        // 注意这里的config.headers.token中的.token的字段是后端定义的，有的不是token这个名字
        config.headers.token = getToken()
    }

    return config //放行 不return 那么不能将请求真正的发送出去
})

//全局响应拦截 统一对错误进行处理

request.interceptors.response.use(config => {
    console.log(config);
    // element关闭加载动画
    loadingIns.close()

    switch(config.data.code){
        // case 200:  //一般过期的转态接口需要 等很久，我们都可以用 200的状态码来测试 自己的代码逻辑是否正确  查看本地存储是否清除掉
        case 401:
            // 登陆过期，清除缓存
            localStorage.removeItem('token')
            localStorage.removeItem('userInfo')
            localStorage.removeItem('role')
            ElMessage ({
                type:'message',
                message:'登陆状态过期，请重新登陆',
                duration:1500,
                onClose:()=>{
                    router.push('/login')
                }
            })
            break
        case 403:
            // 用户未登录
            ElMessage ({
                type:'message',
                message:'用户未登录请登录访问',
                duration:1500,
                onClose:()=>{
                    router.push('/login')
                }
            })
            break
        default:
            break

    }
    // 后端检验token 失败，返回 错误code 401 token过期 (不对) 403 未登录
    // 在响应拦截器中 判断code 如何token过期或未登录 弹出错误提示，清除本地所有登录相关缓存

    // console.log('config', config)
    
    // 10022 1004 这两个状态码分别对应了token过期和session过期
    // 10026 没有权限
    // 清掉本地缓存 清掉token
    // let { code } = config.data;

    // if (code === '10022' || code === '1004') {
    //     Message.error('登入过期,请重新登入')
    //     storage.rm('qf-token')
    //     location.reload()
    // }
    
    return config //如果不return axios请求就拿不到响应结果
})

export default request;