<template>
    <div class="main">
        <h1>登陆页</h1>
        <div class="box">
        
            <el-form :model="numberValidateForm" ref="numberValidateForm" label-width="100px" class="demo-ruleForm">
                <el-form-item
                    label="用户名"
                    prop="userName"
                    :rules="[
                        { required: true, message: '用户名不能为空' },
                        { type: 'string', message: '用户名必须为' }
                    ]"
                >
                    <el-input v-model="numberValidateForm.userName" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item
                    label="密码"
                    prop="passWord"
                    :rules="[
                        { required: true, message: '密码不能为空' },
                        { type: 'string', message: '密码必须为' }
                    ]"
                >
                    <el-input v-model="numberValidateForm.passWord" autocomplete="off" type="password"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('numberValidateForm')">登陆</el-button>
                    <el-button @click="resetForm('numberValidateForm')">重置</el-button>
                </el-form-item>
                <el-form-item>
                    <el-radio-group v-model="role" class="ml-4">
                    <el-radio label="admin" size="large">管理员登录</el-radio>
                    <el-radio label="普通用户" size="large">普通用户登录</el-radio>
                    </el-radio-group>
                    {{ role }}
                    
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>
<script>
// 
import {userLogin,getAuthAdmin,getAuthPepole} from '@/api/login'

export default{
    name:'LoginPage',
    data(){
        
        return {
        numberValidateForm: {
          userName: '',
          passWord:''
        },
        // 登陆角色 默认admin
        role:'admin'
      };

    },
    methods:{
        // 提交登陆
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$router.replace('/home')
                    // alert('submit!');
                    // this.userLoginApiFn()

                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        // 重置表单
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        // 登陆接口方法
        userLoginApiFn(){

            userLogin(this.numberValidateForm).then(res=>{
                // console.log(res);
                if(res.data.code==200){
                    console.log(res);

                    // 用户登陆成功，把用户信息存储在vuex中
                    this.$store.commit('user/INIT_USER',res.data.data)

                    // 本地存储可以封装一个方法

                    // 因为vuex是刷新页面是会丢失的，
                    // vuex的持久化存储，使用本地logcalstorage进行备份 给vuex使用
                    const {token,userInfo,role}=res.data.data
                    localStorage.setItem('token',token)
                    // 注意本地存储时字符串token和role可以直接存储，但是userInfo是个对象不能直接存储
                    localStorage.setItem('userInfo',JSON.stringify(userInfo))
                    localStorage.setItem('role',role)

                    // 根据登陆后，接口返回的role角色 作为参数进行发送 返回菜单路由的接口，实际开发中没有if判断role 这里我们使用的是mock的接口
                    if(this.role=='admin'){
                        // admin接口
                        getAuthAdmin({
                            isrole:role
                        }).then(res=>{
                            // console.log(res);
                            // 缓存 返回导航和路由数据 持久化
                            const { menus, authRoutes } = res.data.data
                            localStorage.setItem('menus', JSON.stringify(menus))
                            localStorage.setItem('authRoutes', JSON.stringify(authRoutes))
                            // 将menus 存储到vuex中
                            this.$store.commit('user/INIT_ROLE_MENUS', menus)
                            // 动态添加当前角色可以访问权限路由
                            this.$store.commit('user/INIT_USER_ROUTES', authRoutes)
                            // 
                            this.$message({
                                type: 'success',
                                message:'登陆成功',
                                onClose:()=>{
                                    // this.$router.push('/home')
                                    // 登陆成功跳转到主页面
                                    this.$router.replace('/home')
                                }
                            })
                        })
                    }else{
                        // 普通用户接口
                        getAuthPepole({
                            isrole:role
                        }).then(res=>{
                            // console.log(res)
                            // 缓存 返回导航和路由数据 持久化
                            const { menus, authRoutes } = res.data.data
                            localStorage.setItem('menus', JSON.stringify(menus))
                            localStorage.setItem('authRoutes', JSON.stringify(authRoutes))
                            // 将menus 存储到vuex中
                            this.$store.commit('user/INIT_ROLE_MENUS', menus)
                            // 动态添加当前角色可以访问权限路由
                            this.$store.commit('user/INIT_USER_ROUTES', authRoutes)
                            // 
                            this.$message({
                                type: 'success',
                                message:'登陆成功',
                                onClose:()=>{
                                    // this.$router.push('/home')
                                    // 登陆成功跳转到主页面
                                    this.$router.replace('/home')
                                }
                            })
                        })
                    }

                    
                }else{

                }
            })
        },

        
    
    }
}
</script>
<style lang="less" scoped>
.main{
    height: 100%;
    width: 100%;
    background: url(@/assets/images/login-background.png) no-repeat center center;
    background-size: 100% 100%;
    position: relative;
    .box{
        background-color: gray;
        position: absolute;
        right: 20%;
        top: 40%;
        width: 30%;
        height: 30%;
        padding: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
</style>